<template>
	<div class="store-visit-action-service-home">
		<top-navigation></top-navigation>
		<h3 class="title">Store Visit Action Service</h3>
		<div class="button-container">
			<span v-if="canWrite">
				<button type="button" class="button new" @click="newWalk">Create New Walk</button>
				<button type="button" class="alert button existing" @click="existingWalk">Retrieve Existing Walk</button>
			</span>
			<span v-else>
				<button type="button" class="alert button existing" @click="readOnlyWalk">Retrieve Walk</button>
			</span>
		</div>
	</div>
</template>

<style type="text/css" lang="scss" scoped>
	.title{
		text-align: center;
		margin-bottom: 50px;
		color: #1B376B;
	}
	.button-container{
		text-align: center;

		.button{
			margin-right: 25px;
		}
	}
</style>

<script type="text/javascript">
	import Vue from 'vue'
	import TopNavigation from '../TopNavigation'
	import VueRouter from 'vue-router'
	import Auth from '../../Auth'
	
	Vue.use(VueRouter);

	export default{
		name: 'store-visit-action-service-home',
		components:{
			'top-navigation': TopNavigation
		},
		data: function(){
			return {

			}
		},
		created: function(){
			
		},
		mounted: function(){
			
		},
		computed: {
			canWrite: function(){
				if(Auth.userPermission() == 'write')
					return true
				return false
			}
		},
		methods: {
			newWalk: function(){
				const router = new VueRouter();
				router.push('/store-visit-action-service/create')
			},
			existingWalk: function(){
				const router = new VueRouter();
				router.push('/store-visit-action-service/edit')
			},
			readOnlyWalk: function(){
				const router = new VueRouter();
				router.push('/store-visit-action-service/list')
			},
		}
	}
</script>


