<template>
  <div class="form-selector">
    <div class="container picker">
        <div class="header">
            <img src="http://sitesforchurch.s3.amazonaws.com/PGA_Horiz_Logo_BLUE.jpg" width="322" height="79">
            <h2>Store Walkthrough Forms</h2>
        </div><!--/.header-->
        <h3><span>Sign In</span></h3>
        <form>
            <div class="row">
                <div class="large-12 columns">
                    <label>Name
                        <v-select :debounce="250" :on-search="getUsers" v-model="user" :options="users" placeholder="Select User"></v-select>
                    </label>
                </div>
            </div>
            <div class="row">
                <div class="large-12 columns">
                    <label>4 Digit Pin
                        <input id="pin" type="password" v-model="pin"/>
                    </label>
                </div>
            </div>
            <div class="row">
                <div class="large-12 columns">
                    <button @click="signIn" type="button" class="alert button" :disabled="loading">Sign In</button>
                </div>
            </div>
        </form>

    </div>
  </div>
</template>
<script>
import Airtable from 'airtable'
import Auth from '../Auth'
import VueRouter from 'vue-router'
import vSelect from "vue-select"

export default {
  name: 'Login',
  components:{
    vSelect
  },
  watch: {
    user: function(val, oldVal) {
      // Focus to pin when participant is selected
      document.getElementById('pin').focus();
    }
  },
  data: function(){
      return {
        users: [],
        user: "",
        pin: "",
        base: null,
        loading: false
      }
  },
  mounted: function(){
    // If user is loggedin then redirect it to home page
    if(Auth.userLoggedIn()){
      const router = new VueRouter();
      router.push('/form-selector');
    }
    
    // Configure Airtable
    Airtable.configure({ apiKey: 'keyIyJzQotiRwef4b' });
    this.base = Airtable.base('appigpdvUi9lnZxuM'); 
  },
  created: function(){
      
  },
  methods: {
    signIn: function(){
      var self = this;
      // Validate
      if(this.user == "" || this.user == null || this.pin == ""){
        alert("User field and Pin Field can not be empty")
        return false;
      }

      this.loading = true;
      Auth.login(this.user.value,this.pin).then(function(response){        
        if(response == true){
          const router = new VueRouter();
          router.push('/form-selector');
        }else{
          alert("Unable to login");
        }
        self.loading = false;
      }).catch(function(error){
        console.log(error)
        alert("Unable to login");
        self.loading = false;
      })
      
    },
    getUsers: function(search,loading){
      var self = this;
      loading(true)
      
      var args = {
        view: 'Grid view',
        fields: ['Name','First Name','Last Name'],
        filterByFormula: "SEARCH(LOWER(TRIM('"+search+"')),LOWER(TRIM({Name})))",
      };
      this.base('Names').select(args).eachPage(function page(records, fetchNextPage) {        
        self.users = []
        records.forEach(function(item,key){
          self.users.push({            
            label: item.fields['Name'],
            value: item.id
          });          
        });
        loading(false)
      },function(err){
        loading(false)
      });
    }
  }
}
</script>


