<template>
  <div class="form-selector">
    <top-navigation></top-navigation>
    <div class="container picker">
    <div class="header">
    <img src="http://sitesforchurch.s3.amazonaws.com/PGA_Horiz_Logo_BLUE.jpg" width="322" height="79">
  </div><!--/.header-->
  <h3><span>Forms</span></h3>
  <ul class="menu vertical Forms">
    <li class="action">
      <span class="heading">Store Visit Action</span>
      <router-link v-if="canWrite" class="button" to="/store-visit-action/create">Create New Walk</router-link>
      <router-link v-if="canWrite" class="button alert" to="/store-visit-action/edit">Retrieve Existing Walk</router-link>
      <router-link v-else class="button alert" to="/store-visit-action/list">Retrieve Walk</router-link>      
    </li>
    <li class="action">
      <span class="heading">Store Visit Action Service</span>      
      <router-link v-if="canWrite" class="button" to="/store-visit-action-service/create">Create New Walk</router-link>
      <router-link v-if="canWrite" class="button alert" to="/store-visit-action-service/edit">Retrieve Existing Walk</router-link>
      <router-link v-else class="button alert" to="/store-visit-action-service/list">Retrieve Walk</router-link>      
    </li>
  </ul>
  <h3><span>Standards</span></h3>
  <ul class="menu vertical guidelines">
    <li v-for="(item,index) in items" >
      <a :href="item.fields['Attachments'] != undefined ? item.fields['Attachments'][0]['url'] : 'javascript:void(0)'" download >        
        <img :src="item.fields['Thumbnail'][0]['thumbnails']['large']['url']" />
        <span>{{ item.fields.Name }}</span>
      </a>      
    </li>
  </ul>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
import Auth from '../Auth'
import TopNavigation from './TopNavigation'

export default {
  name: 'form-selector',
  components: {
    TopNavigation
  },
  data:function(){
    return {
      items: [],
      loading_items: false
    };
  },
  mounted:function(){
    this.getList();
  },
  computed:{
    canWrite: function(){
      const permission = Auth.userPermission()      
      if(permission == 'read')
        return false
      return true
    }
  },
  methods: {
    getList:function(){
      var _self = this; 
      axios.get('https://api.airtable.com/v0/appuaxxb9Ef54NjPR/Merchandise%20Standards?api_key=keyRTLlrVS02vC3Vx').then(function (response) {
        console.log(response.data)
          if(response.data.records){
            _self.items = response.data.records;
          }
          _self.loading_items = false;
        }).catch(function (error) {
          _self.loading_items = false;
        });
      }         
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .form-selector {
    width: 100%;
    height: 100%;
    min-height: 200vh;
    /*background: #072FB8;*/
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
  }
  .container.picker {
    width: 100%;
    max-width: 768px;
    margin-left: auto;
    margin-right: auto;
    background: #ffffff;
    padding: 22px 0;
    min-height: 120vh;
    z-index: 1;
  }
  .header img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 22px;
  }
  .header h2 {
    text-align: center;
    font-weight: 800;
    text-transform: uppercase;
    color: #C31B36;
    /*font-size: 29px;*/
    margin-bottom: 30px;
  }
  h3 {
    font-weight: 800;
    text-transform: uppercase;
    color: #B42F3B;
    text-align: left;
    margin: 30px 0;
  }
  ul.menu.vertical {
    background: #F1F1F1;
    font-size: 24px;
    padding: 20px 0;

    .heading{
      color: black;
      border-bottom: 1px solid black;
      display: block;
      margin-bottom:15px;
    }
  }
  ul.menu.vertical li {
    margin-bottom: 25px;
  }
  ul.menu.vertical li:last-child {
    margin-bottom: 0;
  }
  ul.menu.vertical li a {
    color: #C31B36;
    text-decoration: underline;
  }
  ul.menu.vertical.guidelines{
    padding: 0;
    background: transparent;

    li{
      display: inline-block;
      text-align: left;
      margin: 0;

      a{
        padding: 0;
        margin: 0 30px 30px 0;
        text-decoration: none;

        span{
          color: black;
          font-size: 16px;
          display: inline-block;
          border: 0;
          margin-top: 10px;
          font-weight: bold;
        }

        img{
          width: 150px;
          display: block;
        }
      }
    }
  }

  .menu.vertical.Forms{
    li.action{
      padding: 0 1rem;
      a{
        display: inline-block;
        color: white;
        text-decoration: none;
      }
    }
  }
</style>



