                _self.ptf_sc_files = {
                  'files': files
                }
              }
              _self.ptf_sc_rating = record.get("PTF SC Rating") != undefined ? record.get("PTF SC Rating") : 0;
              _self.ptf_sc_hitting_strips = record.get("PTF SC Hitting Strips") != undefined ? record.get("PTF SC Hitting Strips") : false;
              _self.ptf_sc_stance_mats = record.get("PTF SC Stance Mats") != undefined ? record.get("PTF SC Stance Mats") : false;
              _self.ptf_sc_screen = record.get("PTF SC Screen") != undefined ? record.get("PTF SC Screen") : false;
              _self.ptf_sc_side = record.get("PTF SC Side") != undefined ? record.get("PTF SC Side") : false;
              _self.ptf_sc_back_screen = record.get("PTF SC Back Screen") != undefined ? record.get("PTF SC Back Screen") : false;
              _self.ptf_sc_projector = record.get("PTF SC Projector") != undefined ? record.get("PTF SC Projector") : false;
              _self.ptf_sc_projector_inspection_notes = record.get("PTF SC Projector Inspection Notes") != undefined ? record.get("PTF SC Projector Inspection Notes") : '';
              _self.ptf_pcm_rating = record.get("PTF PCM Rating") != undefined ? record.get("PTF PCM Rating") : 0;
              _self.ptf_pcm_confirm = record.get("PTF PCM Confirm") != undefined ? record.get("PTF PCM Confirm") : false;
              _self.ptf_pcm_confirm_ask = record.get("PTF PCM Confirm Ask") != undefined ? record.get("PTF PCM Confirm Ask") : false;
              _self.ptf_pcm_confirm_register = record.get("PTF PCM Confirm Register") != undefined ? record.get("PTF PCM Confirm Register") : false;
              _self.ptf_pcm_confirm_register_inspection_notes = record.get("PTF PCM Confirm Register Inspection Notes") != undefined ? record.get("PTF PCM Confirm Register Inspection Notes") : '';
              _self.ptf_pcm_audit = record.get("PTF PCM Audit") != undefined ? record.get("PTF PCM Audit") : false;
              _self.ptf_pcm_audit_inspection_notes = record.get("PTF PCM Audit Inspection Notes") != undefined ? record.get("PTF PCM Audit Inspection Notes") : '';
            }
            _self.loading = false;
        }, function done(error) {
            console.log(error);
            _self.loading = false;
        });
      }
    },
  },
  mounted(){
    $(document).foundation();
    this.loadData();
    this.loadPDFLinks();
  }
}
</script>

