import Vue from 'vue'
import Airtable from 'airtable'

// Configure Airtable
Airtable.configure({ apiKey: 'keyIyJzQotiRwef4b' });
const base = Airtable.base('appigpdvUi9lnZxuM');

export default {
	login(user,pin){
		return new Promise(function(resolve,reject){
			var args = {
				view: 'Grid view',        
				filterByFormula: "AND({Pin} = '"+pin+"',RECORD_ID() = '"+user+"')",
				fields: ['Name','First Name','Last Name','Title','Email','Notes','Has Edit Access']
			};
			base("Names").select(args).eachPage(function page(records, fetchNextPage){
				if(records.length > 0){
					/* Logged in */

					// Set user to localstorage
					const user = records[0];
					localStorage.setItem("user", JSON.stringify(user._rawJson));

					resolve(true);
				}else{
					resolve(false);
				}
			});
		});
	},
	userLoggedIn(){
		var user = localStorage.getItem("user")
		if(user != null){
			return user;
		}
		return false;
	},

	logout(){
		localStorage.removeItem("user");
    },
    
    userPermission(){
        var user = localStorage.getItem("user")
		if(user != null){
            user = JSON.parse(user);
            if(user['fields']['Has Edit Access'] != undefined && user['fields']['Has Edit Access'] == true)
                return 'write'
            else 
                return 'read'
		}
		return 'read';
    }

};


