<template>
  <div class="my-dropzone">
    <div class="dropzone" :id="id" style="border: 2px dashed rgba(0, 0, 0, 0.3); text-align: center;">
      <div class="dz-message" data-dz-message>
        <svg fill="#A9A9A9" width="60pt" height="60pt" version="1.1" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
          <path d="m62.824 39.391c-8.375 0-15.164 6.7891-15.164 15.168s6.7891 15.164 15.164 15.164c8.3789 0 15.164-6.7891 15.164-15.164 0-8.3789-6.7891-15.168-15.164-15.168zm20.699-18.301h-3.7031c-2.6094-6.3984-2.6758-9.2266-9.1211-9.2266h-18.449c-6.4492 0-6.5977 2.9961-9.1211 9.2266h-26.652c-7.4531 0-13.5 6.043-13.5 13.5v40.051c0 7.457 6.0469 13.5 13.5 13.5h67.051c7.4531 0 13.5-6.043 13.5-13.5v-40.051c-0.003906-7.457-6.0469-13.5-13.504-13.5zm-66.57 18.363c-2.5625 0-4.6406-2.0781-4.6406  -4.6406s2.0742-4.6406 4.6406-4.6406c2.5625 0 4.6406 2.0781 4.6406 4.6406s-2.0781 4.6406-4.6406 4.6406zm45.871 37.547c-12.395 0-22.441-10.047-22.441-22.441 0-12.398 10.047-22.445 22.441-22.445s22.441 10.051 22.441 22.445c0 12.391-10.043 22.441-22.441 22.441z"/>
        </svg>
      </div>
      </div>
  </div>
</template>

<script>
import Dropzone from 'dropzone'
import '../../node_modules/dropzone/dist/dropzone.css'

Dropzone.autoDiscover = false;

export default {
  props: [
    'id',
    'defaultFiles'
  ],
  name: 'my-dropzone',
  data () {
    return {
      files: [],
      dropZone: null
    }
  },
  watch: {
    defaultFiles:function(files){
      if(this.files.length <= 0)
        this.loadDefaultFiles(files);
    }
  },
  mounted(){

    var _self = this;
    var myDropzone = new Dropzone(document.getElementById(this.id), {
      addRemoveLinks: true,
      acceptedFiles: 'image/*',
      dictDefaultMessage: "",
      url: 'http://uploads.im/api',
      previewsContainer: "#"+this.id,
      uploadMultiple: true,
      paramName: 'upload',
      headers: {
        'Cache-Control': '',
        'X-Requested-With': null
      },
      success:function(file,response){
        file.serverFileName = response.data.img_name;
        _self.files.push({
          file_url: response.data.img_url,
          file_info: file
        });
        _self.updateFiles();
      }
    });
    this.dropZone = myDropzone;
    myDropzone.on('removedfile', function (file) {
      _self.files.forEach(function(item,index){
        if(file.serverFileName == item.file_info.serverFileName){
          _self.files.splice(index, 1);
          return;
        }
      })
      _self.updateFiles();
    });
    this.clear();
  },
  methods:{
    clear(){
      this.dropZone.removeAllFiles();
      this.files = [];
    },
    updateFiles(){
      var _self = this;
      this.$emit("input",{
        files: _self.files
      });
      Bus.$emit("dropzoneUpdated");
    },
    loadDefaultFiles(files){
      var _self = this;
      if(files != undefined && files['files'] != undefined && files['files'].length > 0){
        files['files'].forEach(function(item,index){
          // Create the mock file:
          var mockFile = { name: item.file_info['filename'], size: item.file_info['size'] };

          // Call the default addedfile event handler
          _self.dropZone.emit("addedfile", mockFile);

          // Make sure that there is no progress bar, etc...
          _self.dropZone.emit("complete", mockFile);

          // And optionally show the thumbnail of the file:
          _self.dropZone.emit("thumbnail", mockFile, item.file_url);

          _self.files.push({
            file_url: item.file_url,
            file_info: {
              filename: item.file_info['filename'],
              size: item.file_info['size'],
            }
          });

        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.dropzone {
  border: 2px dashed rgba(0, 0, 0, 0.3);
}
</style>



