              _self.action_items_2_done = record.get("Action Items 2 Done") != undefined ? record.get("Action Items 2 Done") : false;
              _self.action_items_3_done = record.get("Action Items 3 Done") != undefined ? record.get("Action Items 3 Done") : false;
              _self.action_items_4_done = record.get("Action Items 4 Done") != undefined ? record.get("Action Items 4 Done") : false;
              _self.action_items_5_done = record.get("Action Items 5 Done") != undefined ? record.get("Action Items 5 Done") : false;
              _self.action_items_1_done_date = record.get("Action Items 1 Done Date") != undefined ? record.get("Action Items 1 Done Date") : '';
              _self.action_items_2_done_date = record.get("Action Items 2 Done Date") != undefined ? record.get("Action Items 2 Done Date") : '';
              _self.action_items_3_done_date = record.get("Action Items 3 Done Date") != undefined ? record.get("Action Items 3 Done Date") : '';
              _self.action_items_4_done_date = record.get("Action Items 4 Done Date") != undefined ? record.get("Action Items 4 Done Date") : '';
              _self.action_items_5_done_date = record.get("Action Items 5 Done Date") != undefined ? record.get("Action Items 5 Done Date") : '';


              // Followup Items
              _self.follow_up_items_1 = record.get("Follow up Items 1") != undefined ? record.get("Follow up Items 1") : '';
              _self.follow_up_items_2 = record.get("Follow up Items 2") != undefined ? record.get("Follow up Items 2") : '';
              _self.follow_up_items_3 = record.get("Follow up Items 3") != undefined ? record.get("Follow up Items 3") : '';
              _self.follow_up_items_4 = record.get("Follow up Items 4") != undefined ? record.get("Follow up Items 4") : '';
              _self.follow_up_items_5 = record.get("Follow up Items 5") != undefined ? record.get("Follow up Items 5") : '';

            }
            _self.loading = false;
        }, function done(error) {
            console.log(error);
            _self.loading = false;
        });
      }
    }
  },
  mounted(){
    $(document).foundation();
    this.loadData();
    this.loadPDFLinks();
  }
}
</script>

