    background: rgba(0,0,0,0.5);
    left: 0;
    right: 0;
    top: 0;
    z-index: 99;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    color: white;
    font-size: 26px;
  }
  span.data {
    font-style: italic;
    font-weight: 800;
  }
</style>



