<template>
	<div class="store-visit-action-service-edit">
		<top-navigation></top-navigation>
		<div class="row">
			<div class="columns medium-10 medium-push-1 main-wrapper">
				<div class="heading">
					<h2>Store Visit Action Service</h2>
					<h3>Choose The Action To Retrieve</h3>	
				</div>
				
				<div class="filters">
					<div class="row">
						<div class="columns medium-4">
							<label>
							Filter By Date:
							<input type="date" v-model="filter_date" />
							</label>
						</div>
						<div class="columns medium-3">
							<label >
							Filter By Store Number:
							<input type="text" v-model="filter_store_number" />
							</label>
						</div>
						<div class="columns medium-2">
						<br>
							<button type="button" class="alert button" @click="handleFilter" :disabled="loading">Filter</button>
						</div>
						<div class="columns medium-1"></div>
					</div>
				</div>

				<table>
					<thead>
						<tr>
							<th width="150">Date of Vist</th>
							<th width="150">Store Number</th>
							<th>Field Support Associate</th>
							<th width="150" style="text-align:center">View</th>
						</tr>
					</thead>
					<tbody>						
						<tr v-for="item in items">
							<td>{{ item.fields['Date of Visit'] | formatDate }}</td>
							<td>{{ item.fields['Store Number'] }}</td>
							<td>{{ item.fields['Field Support Associate'] }}</td>
							<td style="text-align: center"><button @click="update(item.id)" class="button">Edit</button></td>
						</tr>
						<tr v-if="loading == true">
							<td colspan="4" align="center">Loading Items...</td>
						</tr>
						<tr v-if="loading == false && items.length <= 0">
							<td colspan="4" align="center">No Items Found</td>
						</tr>
					</tbody>
				</table>
				<div class="load-more-button-container">
					<button @click="loadMore()" v-if="nextPage" class="button" :disabled="loading">Load More</button>
				</div>
				
			</div>
		</div>
	</div>
</template>

<style type="text/css" lang="scss" scoped>
	.heading{
		text-align: center;

		h2{
			color: #1B376B;
		}
		h3{
			color: #B71B29;
		}
	}

	.load-more-button-container{
		text-align: center;
	}
	.filters{
		margin-top: 50px;
	}
</style>

<script type="text/javascript">
	import Airtable from 'airtable'
	import TopNavigation from '../TopNavigation'
	import VueRouter from 'vue-router'
	import Moment from 'moment'
	import Auth from '../../Auth'

	export default{
		name: "StoreVisitActionServiceEdit",
		components: {
			TopNavigation
		},
		mounted: function(){
			if(Auth.userPermission() == 'read'){
				const router = new VueRouter();
				router.push('/form-selector');
			}
			this.getData();
		},
		created: function(){

		},
		data: function(){
			return {
				page: 1,
				per_page: 10,
				items: [],
				app_id: 'appgdTM8dhKp7xW2c',
      			app_key: 'keyIyJzQotiRwef4b',
      			loading: false,
      			nextPage: null,
				filter_date: '',
				filter_store_number: ''
			}
		},
		filters: {
			formatDate: function(date){
				return Moment(date).format('L');
			}
		},
		methods: {			
			getData: function(){
				var self = this;				
				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				var args = {
					view: 'Grid view',
					pageSize: self.per_page,
					sort: [
						{field: 'Date of Visit', direction: 'desc'}
			        ]
				};

				var filter_string = []
				if(self.filter_date != "" ){
					filter_string.push(" DATETIME_DIFF({Date of Visit}, '"+self.filter_date+"', 'days') = 0 ");
				}
				if(self.filter_store_number != "" ){
					filter_string.push(" LOWER({Store Number}) = LOWER('"+self.filter_store_number+"') ");
				}
				if(filter_string.length > 0){
					filter_string = " AND( " +filter_string.join(' , ') + " )"
					args['filterByFormula'] = filter_string;
				}
				
				self.items = []
				self.loading = true;
				base('Store Visit Action Services').select(args).eachPage(function page(records, fetchNextPage) {
					if(records.length > 0){
						$.each(records,function(key,record){
							self.items.push(record);	
						});					

						if(records.length < self.per_page){
							self.nextPage = null;
						}else{
							self.nextPage = fetchNextPage;	
						}

						
					}else{
						self.nextPage = null;
					}
					self.loading = false;
				},function(err){
					self.nextPage = null;
				});
			},
			update: function(record_id){
				if(record_id){
					const router = new VueRouter();
					router.push('/store-visit-action-service/update/'+ record_id);	
				}
			},
			loadMore: function(){
				if(this.nextPage){
					this.loading = true;
					this.nextPage();
				}
			},
			handleFilter: function(){
				const self = this
				self.getData();
			}
		}
	}
</script>


