import Vue from 'vue'
import Router from 'vue-router'
import StoreVisitActionHome from '@/components/StoreVisitAction/StoreVisitActionHome'
import StoreVisitActionCreate from '@/components/StoreVisitAction/StoreVisitActionCreate'
import StoreVisitActionEdit from '@/components/StoreVisitAction/StoreVisitActionEdit'
import StoreVisitActionList from '@/components/StoreVisitAction/StoreVisitActionList'
import StoreVisitActionUpdate from '@/components/StoreVisitAction/StoreVisitAction'
import StoreVisitActionSingle from '@/components/StoreVisitAction/StoreVisitActionSingle'

// Service
import StoreVisitActionServiceHome from '@/components/StoreVisitActionService/StoreVisitActionServiceHome'
import StoreVisitActionServiceCreate from '@/components/StoreVisitActionService/StoreVisitActionServiceCreate'
import StoreVisitActionServiceEdit from '@/components/StoreVisitActionService/StoreVisitActionServiceEdit'
import StoreVisitActionServiceList from '@/components/StoreVisitActionService/StoreVisitActionServiceList'
import StoreVisitActionServiceUpdate from '@/components/StoreVisitActionService/StoreVisitActionService'
import StoreVisitActionServiceSingle from '@/components/StoreVisitActionService/StoreVisitActionServiceSingle'
import FormSelector from '@/components/FormSelector'
import Login from '@/components/Login'

import Auth from '../Auth'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'Login',
			component: Login
		},
		{
			path: '/form-selector',
			name: 'FormSelector',
			component: FormSelector,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action',
			name: 'StoreVisitActionHome',
			component: StoreVisitActionHome,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/create',
			name: 'StoreVisitActionCreate',
			component: StoreVisitActionCreate,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/edit',
			name: 'StoreVisitActionEdit',
			component: StoreVisitActionEdit,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/list',
			name: 'StoreVisitActionList',
			component: StoreVisitActionList,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/update/:id',
			name: 'StoreVisitActionUpdate',
			component: StoreVisitActionUpdate,
			props:true,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/update/',
			name: 'StoreVisitActionUpdateNoParam',
			component: StoreVisitActionUpdate,
			props:true,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action/single/:id',
			name: 'StoreVisitActionSingle',
			component: StoreVisitActionSingle,
			props:true
		},
		// Service
		{
			path: '/store-visit-action-service',
			name: 'StoreVisitActionServiceHome',
			component: StoreVisitActionServiceHome,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action-service/create',
			name: 'StoreVisitActionServiceCreate',
			component: StoreVisitActionServiceCreate,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action-service/edit',
			name: 'StoreVisitActionServiceEdit',
			component: StoreVisitActionServiceEdit,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action-service/list',
			name: 'StoreVisitActionServiceList',
			component: StoreVisitActionServiceList,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action-service/update/:id',
			name: 'StoreVisitActionServiceUpdate',
			component: StoreVisitActionServiceUpdate,
			props:true,
			beforeEnter: (to, from, next) => {
				// Check if user loggedin
				if(!Auth.userLoggedIn()){
					next('/');
				}else{
					next();
				}
			}
		},
		{
			path: '/store-visit-action-service/single/:id',
			name: 'StoreVisitActionServiceSingle',
			component: StoreVisitActionServiceSingle,
			props:true
		},
	]
})



