<template>
  <div class="store-visit-action-mail">    
    <div id="mail-modal">
      <button @click="close()" class="btn-close"><i class="fa fa-close"></i></button>
      <div class="row">
        <div class="columns medium-10 medium-push-1">
          <form @submit.prevent="add">
            <fieldset class="fieldset" id="create">
              <legend>Add Recipients</legend>
              <v-select v-model="email" :options="emails" multiple></v-select>
              <!--<input type="email" name="" required="" v-model="email" autofocus="">-->
              <br/>
              <button type="submit" class="alert button" :disabled="email == ''">Add Recipient</button>
            </fieldset>
          </form>

          <fieldset class="fieldset" id="list">
            <legend>Recipients</legend>
            <ul>
              <li v-for="(recipient,index) in recipients">{{ recipient['label'] }} <a href="javascript:void(0)" class="close" @click="remove(index)"><i class="fa fa-close"></i></a></li>
            </ul>
          </fieldset>

          <div class="send-button-container">
            <button class="btn-send alert button" :disabled="loading || recipients.length <= 0" @click="send">Send Walk Details</button>
          </div>
        </div>
      </div>  
    </div>
  </div>
</template>
<script type="text/javascript">
  import axios from 'axios'
  import {WS_URL} from '../../config'
  import vSelect from 'vue-select'
  import Airtable from 'airtable'

  export default{
    name: 'StoreVisitActionMail',
    components: {
      vSelect
    },
    data: function(){
      return {
        email: [], // Selected email
        emails: [],
        recipients: [],
        loading: false,
        directory_app_id : 'appigpdvUi9lnZxuM',
				app_id: 'appHvlnfstue6skcB',
        app_key: 'keyIyJzQotiRwef4b',
      }
    },
    props: ['store'],
    created: function(){
      this.getUserEmails();
    },
    methods: {
      close: function(){
        Bus.$emit('storeVisitActionMailClose');
      },
      add: function(){
        const self = this
        if(this.email){
          $.each(this.email,function(k,v){
            self.recipients.push(v)
            self.email = []
          })          
        }
      },
      remove: function(index){
        if (index > -1) {
          this.recipients.splice(index, 1);
        }
      },
      send: function(){
        if(this.recipients.length > 0){
          const self = this;
          self.loading = true;
          axios({
            method: 'post',
            url: WS_URL+'/store-visit-action/send-mail',
            data: {
              recipients: self.recipients,
              store_id: self.store['id'],
              store_number: self.store['fields']['Store Number'],
              date_of_visit: self.store['fields']['Date of Visit'],
              field_support_associate: self.store['fields']['Field Support Associate'],
            }
          }).then(function(response){
            self.loading = false;
            self.email = []
            self.recipients = []
          }).catch(function(error){
            self.loading = false;
          });
        }        
      },
      getUserEmails: function(){
        const self = this;
        Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.directory_app_id);
				self.loading = true;

        self.emails = [];

        const args = {
          fields: ['Name','Email']
        }
				
        base('Names').select(args).eachPage(function page(records, fetchNextPage) {
          $.each(records,function(k,v){
            const name = v.get("Name")
            const email = v.get("Email")
            self.emails.push({
              label: name,
              value: email
            })
          })

          self.emails.push(records);
          self.loading = false
          fetchNextPage();              
        });
        
      }
    }
  }
</script>
<style type="text/css" lang="scss" scoped="">  
.store-visit-action-mail{
  z-index: 9999;
}
  #mail-modal{
    background: #E5E5E5;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 9999;
    padding-top: 65px;

    fieldset{
      margin-bottom: 50px;
      
      legend{
        background: transparent;
        font-size: 1.4rem;
        text-transform: uppercase;
        font-weight: bold;
        color: #1B376B;
      }
    }

    .btn-close{
      position: absolute;
      padding: 25px;
      left:0;
      top: 0;
      font-size:32px;
      cursor: pointer;
    }

    #list{
      ul{
        margin: 0;
        padding: 0;

        li{
          display: inline-block;
          background: white;
          padding: 8px 40px 8px 8px;
          margin-right: 10px;
          border-radius: 20px;
          position: relative;
          margin-bottom: 15px;

          .close{
            position: absolute;
            right: 0;
            vertical-align: middle;
            width: 25px;
            text-align: center;
            height: 25px;
            color: #B71B29;
            top: calc( 50% - 13px );
            right: 10px;
          }
        }
      }      
    }

    .send-button-container{
      text-align: center;
    }

  }
</style>


