<template>
	<div class="store-visit-action-create">
		<top-navigation></top-navigation>
		<div class="row">
			<div class="columns medium-10 medium-push-1 main-wrapper">
				<fieldset class="fieldset">
					<legend class="title">Visit Info</legend class="title">
					<div class="row">
						<label for="Store Number" class="columns medium-4">Store Number
							<input type="text" name="Store Number" v-model="store_number">
						</label>
					</div><!--row-->
					<div class="row">
						<label for="Field Support Associate" class="columns medium-6">Field Support Associate
						<input type="text" name="Field Support Associate" v-model="field_support_associate">
						</label>
					</div><!--row-->
					<div class="row">
						<label for="Date of Visit" class="columns medium-6">Date of Visit
						<input type="date" name="Date of Visit" v-model="date_of_visit">
						</label>
						<label for="Attendees from store" class="columns medium-6">Attendees from store
							<input type="text" name="Attendees from store" v-model="attendees_from_store">
						</label>
					</div><!--/row-->
				</fieldset>
				<button type="button" class="button" @click="create()" :disabled="loading">{{ !loading ? "Create Walk" : "Processing..." }}</button>
			</div>
		</div>
	</div>
</template>

<style type="text/css" lang="scss" scoped>
	fieldset{
		margin-bottom: 20px;
	}
</style>

<script type="text/javascript">
	import Airtable from 'airtable'
	import moment from 'moment'
	import TopNavigation from '../TopNavigation'
	import VueRouter from 'vue-router'
	import Auth from '../../Auth'

	export default{
		name: 'StoreVisitActionCreate',
		components:{
			'top-navigation': TopNavigation
		},
		data(){
			return {
				app_id: 'appHvlnfstue6skcB',
      			app_key: 'keyIyJzQotiRwef4b',
      			loading: false,
				rec_id: '',
				record: null,
				store_number: '',
				field_support_associate: '',
				date_of_visit: '',
				attendees_from_store: '',
			}
		},
		mounted: function(){
			if(Auth.userPermission() == 'read'){
				const router = new VueRouter();
				router.push('/form-selector');
			}
		},
		methods: {
			create: function(){
				var self = this;
				if(this.store_number == "" || this.date_of_visit == ""){
					alert("Store Number and Date of Visit field is required");
					return;
				}
				var promise = this.checkIfExist();
				promise.then(function(isExist) {
					if(!isExist){
						// Create
						self.loading = true;
						Airtable.configure({ apiKey: self.app_key });
      					const base = Airtable.base(self.app_id);
      					const get_record_data = () => {
							return {
								"Store Number": self.store_number,
								"Date of Visit": self.date_of_visit,
								"Field Support Associate": self.field_support_associate,
								"Attendees from Store": self.attendees_from_store,
							}
						};
      					base('Store Visit Action')
							.create(get_record_data(), function(err, record) {
								if (err) {
									self.loading = false;
									alert(err);
									return;
								}
								self.record = record;
								self.rec_id = record.getId();
								self.loading = false;
								alert("Record Created");

								// Go to update view
								const router = new VueRouter();
								router.push('/store-visit-action/update/'+self.rec_id)
							});
					}else{
						// Record Already Exist
						alert("Record already exist with this store number and date of visit.")
					}
				}, function(error) {
					console.log(error);
					alert("Unable to process request.")
				});
			},
			checkIfExist(){
				var self = this;
				this.loading = true;
				var promise = new Promise(function(resolve,reject){					
					// Check only if store number and date of visit are non empty values
					if(self.store_number != "" && self.date_of_visit != ""){
						Airtable.configure({ apiKey: self.app_key });
						const base = Airtable.base(self.app_id);
						var date = moment(self.date_of_visit).format('M/DD/YYYY');
						var args = {
							view: 'Grid view',
							filterByFormula: 'AND({Store Number}="'+self.store_number+'",DATETIME_DIFF({Date of Visit}, "'+date+'" , "days") = 0)'
						};
						base('Store Visit Action').select(args).eachPage(function page(records, fetchNextPage) {
	            			if(records.length > 0){
	            				self.loading = false;
	            				resolve(true);
	            			}else{
	            				self.loading = false;
	            				resolve(false);
	            			}
				        }, function done(error) {
				        	self.loading = false;
				            resolve(false);
				        });
					}else{
						self.loading = false;
						resolve(false);
					}
				});

				return promise;
				
			}
		}
	}
</script>


