<template>	
	<div>
		<div class="top-navigation">
			<div class="row">
				<div class="top-bar">
					<div class="top-bar-left">
						<ul class="dropdown menu" data-dropdown-menu>
							<li class="menu-text">
								<a href="#"><i class="fa fa-home" aria-hidden="true"></i></a>
							</li>
							<li>
								<span class="title">{{ title }}</span>
							</li>
						</ul>
					</div>
					<div class="top-bar-right">					
						<div class="dropdown" v-if="items.length > 0">
							<a class="dropdown-button" href="javascript:void(0)">JUMP TO SECTION</a>
							<ul class="dropdown-content">
								<li v-for="item in items"><a href="javascript:void(0)" @click.prevent="handleClick($event,item.link)">{{ item.title }}</a></li>
							</ul>
						</div>
						<a class="logout" href="javascript:void(0)" @click="logout" v-if="loggedIn"><i class="fa fa-sign-out" aria-hidden="true"></i></a>
					</div>
				</div>
			</div>		
		</div>
		<div class="spacer"></div>
	</div>
</template>

<style type="text/css" lang="scss">
	.top-navigation{
		background: #1B376B;
		position: fixed;
		width: 100%;
		z-index: 9999;

		.title{
			color: white;
			font-weight: bold;
		}
		.top-bar{
			background: #1B376B;			    

			ul{
				background: #1B376B;

				.menu-text{
					a{
						font-size: 24px;
						padding: 0;
						color: white;
					}
				}				
			}

			.top-bar-right{
				padding: 8px;

				.logout{
					padding: 0.7rem 1rem;
					font-size: 24px;
					color: white;
				}
				.dropdown{
					position: relative;
					display: inline-block;

					.dropdown-button{
						color: white;
					    padding: 0.7rem 1rem;
					    border: none;
					    cursor: pointer;
					    display: inline-block;

						&:after{
							border-color: white transparent transparent;
						}
					}

					.dropdown-content{
						display: none;
					    position: absolute;
					    background-color: #f9f9f9;
					    min-width: 160px;
					    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
					    z-index: 1;
					    margin: 0;
					    padding: 0;
					    list-style: none;

					    a{
					    	color: black;
						    padding: 12px 16px;
						    text-decoration: none;
						    display: block;

						    &:hover{
						    	background-color: #f1f1f1
						    }
					    }
					}

					&:hover{
						.dropdown-content{
							display: block;
						}
						.dropdown-button{
							background-color: #365590;
						}
					}
				}
			}
		}
	}	
	.spacer{
		padding-top: 100px;
	}
</style>

<script type="text/javascript">	
	import VueRouter from 'vue-router'
	import Auth from '../Auth'
	export default{
		name: 'top-navigation',
		data: function(){
			return {
				items: [],
				title: ''
			}
		},
		computed: {
			loggedIn: function(){
				return Auth.userLoggedIn() ? true : false;
			}			
		},
		mounted: function(){			
			$(document).ready(function(){
				$(document).foundation();	
			});
		},
		created: function(){
			Bus.$on('topNavigationItems',this.setItems);
			Bus.$on('topNavigationTitle',this.setTitle);
		},
		methods: {
			setItems: function(items){
				this.items = items;				
			},
			setTitle: function(title){
				this.title = title;
			},
			handleClick: function(e,link){
				event.preventDefault();
				if(link == ""){
					return false;
				}else if (link.substring(0, 1) == "#") {
					link = link.replace("#","");
					var element = document.getElementById(link)
					var offset = $(element).offset()
					$('html, body').animate({
						scrollTop: offset.top - ($(".top-navigation .top-bar").height() + 50),
						scrollLeft: offset.left
					});
					 
				}else if(link.substring(0, 4) == "http" || link.substring(0, 5) == "https"){
					window.location = link;
				}
				else{
					const router = new VueRouter();
					router.push(link);
				}
				return false;
			},
			logout: function(){
				Auth.logout();
				const router = new VueRouter();
				router.push('/');
			}
		},		
	}
</script>


