  mounted(){
    if(Auth.userPermission() == 'read'){
      const router = new VueRouter();
      router.push('/form-selector');
    }
    var self = this;
    $(document).ready(function(){
      $("input,textarea").on("blur",function(){
        self.save();
      })
    });
    this.loadPDFLinks();
    this.checkIfExistThenFetch();
    this.getLastFollowupItems();
    Bus.$emit('topNavigationTitle','PGA Tour Superstore - Store Visit Action Service');
    Bus.$emit('topNavigationItems',this.menu_items);
  },
  created(){
    var self = this;
    Bus.$on("storeVisitActionServiceMailClose",function(){
      self.mail_modal = false;
    });
    Bus.$on("storeVisitActionServiceActionItemsClose",function(){
      self.action_items_modal = false;
    });
    Bus.$on("dropzoneUpdated",function(){
      self.save();
    });
    if(!this.id){
      const router = new VueRouter();
      router.push('/store-visit-action/create');
    }

  }
}
