<template>
	<div class="store-visit-action-service-action-items">
		<div id="action-items-modal">
	      <button @click="close()" class="btn-close"><i class="fa fa-close"></i></button>
	      <div class="row">
	        <div class="columns medium-10 medium-push-1">
	        	<form @submit.prevent="create">
					<fieldset class="fieldset" id="create">
						<legend>Add Action Item</legend>
						<input type="text" name="" required="" v-model="action_item" autofocus="">
						<p>
							<input type="radio" name="category" v-model="category" value="Club Services/Repair" id="category_club_services_repair" :checked="category == 'Club Services/Repair'"><label for="category_club_services_repair">Club Services/Repair</label>
                            <input type="radio" name="category" v-model="category" value="Fitting" id="category_fitting" :checked="category == 'Fitting'"><label for="category_fitting">Fitting</label>
                            <input type="radio" name="category" v-model="category" value="Instruction" id="category_instruction" :checked="category == 'Instruction'"><label for="category_instruction">Instruction</label>
                            <input type="radio" name="category" v-model="category" value="Simulators/Practice Bays" id="category_simulators_practice_bays" :checked="category == 'Simulators/Practice Bays'"><label for="category_simulators_practice_bays">Simulators/Practice Bays</label>
                            <input type="radio" name="category" v-model="category" value="Memberships" id="category_memberships" :checked="category == 'Memberships'"><label for="category_memberships">Memberships</label>
                            <input type="radio" name="category" v-model="category" value="Tour Van Experience" id="category_tour_van_experience" :checked="category == 'Tour Van Experience'"><label for="category_tour_van_experience">Tour Van Experience</label>
                            <input type="radio" name="category" v-model="category" value="Tennis" id="category_tennis" :checked="category == 'Tennis'"><label for="category_tennis">Tennis</label>
						</p>

						<button type="submit" class="alert button" :disabled="action_item == '' || loading == true">Add Action Item</button>
					</fieldset>
				</form>

				<!-- Club Services/Repair -->
    			<fieldset class="fieldset" id="list" v-if="club_services_repair_items.length > 0">
					<legend>Club Services/Repair Items</legend>
					<ul>
						<li v-for="single in club_services_repair_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Fitting -->
				<fieldset class="fieldset" id="list" v-if="fitting_items.length > 0">
					<legend>Fitting Items</legend>
					<ul>
						<li v-for="single in fitting_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Instruction -->
				<fieldset class="fieldset" id="list" v-if="instruction_items.length > 0">
					<legend>Instruction Items</legend>
					<ul>
						<li v-for="single in instruction_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Simulators/Practice Bays -->
				<fieldset class="fieldset" id="list" v-if="simulators_practice_bays_items.length > 0">
					<legend>Simulators/Practice Bays Items</legend>
					<ul>
						<li v-for="single in simulators_practice_bays_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Memberships -->
				<fieldset class="fieldset" id="list" v-if="memberships_items.length > 0">
					<legend>Memberships Items</legend>
					<ul>
						<li v-for="single in memberships_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

                <!-- Tour Van Experience -->
				<fieldset class="fieldset" id="list" v-if="tour_van_experience_items.length > 0">
					<legend>Tour Van Experience Items</legend>
					<ul>
						<li v-for="single in tour_van_experience_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

                <!-- Tennis -->
				<fieldset class="fieldset" id="list" v-if="tennis_items.length > 0">
					<legend>Tennis Items</legend>
					<ul>
						<li v-for="single in tennis_items">
							<store-visit-action-service-items-single :item="single"></store-visit-action-service-items-single>
						</li>
					</ul>
				</fieldset>

				<div class="send-button-container">
					<button class="button" type="button" @click="send" :disabled="items.length <= 0 || loading">Send Action Items</button>
				</div>
	        </div>
	      </div>  
	    </div>
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	import Airtable from 'airtable'
	import StoreVisitActionServiceActionItemsSingle from './StoreVisitActionServiceActionItemsSingle'
	import {WS_URL} from '../../config'

	export default{
		name: 'store-visit-action-service-action-items',
		components:{
			'store-visit-action-service-items-single': StoreVisitActionServiceActionItemsSingle
		},
		data: function(){
			return {
				app_id: 'appgdTM8dhKp7xW2c',
				app_key: 'keyIyJzQotiRwef4b',
				category: 'Club Services/Repair',
      			action_item: '',
      			items: [],      
      			loading: false			
			}
		},
		props: [
			'store'
		],
		computed: {
			club_services_repair_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Club Services/Repair'
					})
				}else{
					return []
				}
			},
			fitting_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Fitting'
					})
				}else{
					return []
				}
			},
			instruction_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Instruction'
					})
				}else{
					return []
				}
			},
			simulators_practice_bays_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Simulators/Practice Bays'
					})
				}else{
					return []
				}
			},
			memberships_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Memberships'
					})
				}else{
					return []
				}
            },
            tour_van_experience_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Tour Van Experience'
					})
				}else{
					return []
				}
            },
            tennis_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Tennis'
					})
				}else{
					return []
				}
			}
		},
		mounted: function(){
			Bus.$on('storeVisitActionServiceActionItemsUpdateData',this.getData);
		},
		created: function(){
			this.getData();
		},
		methods: {
			close: function(){
				Bus.$emit('storeVisitActionServiceActionItemsClose');
			},
			create: function(){
				var self = this;
				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				this.loading = true;
				base('Action Items').create({
					"Item": self.action_item,
					"Category": [self.category],
			        "Store Visit Action Service": [self.store.id]
			    }, function(err, record) {
			        if (err) { 
			        	console.log(err); 
			        	alert("Unable to cretae new action item")
			        	self.loading = false;
			        	return;
		        	}
					self.action_item = "";
					self.category = "Club Services/Repair"
		        	self.loading = false;
		        	self.getData();
			    });
			},
			getData: function(){
				var self = this;
				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				var args = {
					view: 'Grid view',
					filterByFormula: '{Store Visit Action Service ID}="'+this.store.id+'"',
				};
				
				self.items = [];
				base('Action Items').select(args).eachPage(function page(records, fetchNextPage) {
					self.items = records;
					fetchNextPage();
				});
			},

			send: function(){
				var self = this;
				if(this.items == ""){
					alert("Please enter some items to send.")
					return false;
				}		
				if(confirm("Are you sure to send action items?") == false){
					return false;
				}		
				self.loading = true;

				var itemsToSend = {
					'club_services_repair_items': this.club_services_repair_items,
					'fitting_items': this.fitting_items,
					'instruction_items': this.instruction_items,
					'simulators_practice_bays_items': this.simulators_practice_bays_items,
					'memberships_items': this.memberships_items,
					'tour_van_experience_items': this.tour_van_experience_items,
					'tennis_items': this.tennis_items,
				};

				var sendItem = new Promise(function(resolve,reject){
					const data = {
						'items': itemsToSend,
						'date' : self.store.fields['Date of Visit'],
						'field_support_associate' : self.store.fields['Field Support Associate'],
						'store_number': self.store.fields['Store Number']
					};

					axios.post(WS_URL+'/store-visit-action-service/send-action-items',data).then(function(response){
						self.loading = false
						alert("Items mail sent.")
					}).catch(function(){						
						self.loading = false
						alert("Unable to send mail.")
					});
				});
			}
		}
	}
</script>
<style type="text/css" lang="scss" scoped="">  
.store-visit-action-action-items{
  z-index: 9999;
}
  #action-items-modal{
    background: #E5E5E5;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 9999;
    padding-top: 65px;
    overflow-y: scroll;

    fieldset{
      margin-bottom: 50px;
      
      legend{
        background: transparent;
        font-size: 1.4rem;
        text-transform: uppercase;
        font-weight: bold;
        color: #1B376B;
      }

      	ul{
	    	padding: 0;
	    	margin: 0;
	    	list-style: none;

	    	li{
				display: block;
				transition: all .4s cubic-bezier(.25,.8,.25,1);
				transition-property: box-shadow;
				background-color: #fff;
				width: 100%;
				margin: 0 4px 16px;
				vertical-align: top;
				overflow: auto;
				position: relative;
				z-index: 1;
				box-shadow: 0 1px 5px rgba(0,0,0,.2), 0 2px 2px rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12);				
				position: relative;				

				&:hover{
				    z-index: 2;
    				box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
				}
				
	    	}
	    }
    }

    .btn-close{
      position: absolute;
      padding: 25px;
      left:0;
      top: 0;
      font-size:32px;
      cursor: pointer;
    }

    .send-button-container{
    	text-align: center;
    }


  }
</style>


