<template>
	<div class="store-visit-action-action-items">
		<div id="action-items-modal">
	      <button @click="close()" class="btn-close"><i class="fa fa-close"></i></button>
	      <div class="row">
	        <div class="columns medium-10 medium-push-1">
	        	<form @submit.prevent="create">
					<fieldset class="fieldset" id="create">
						<legend>Add Action Item</legend>
						<input type="text" name="" required="" v-model="action_item" autofocus="">
						<p>
							<input type="radio" name="category" v-model="category" value="Clubs" id="category_clubs" :checked="category == 'Clubs'"><label for="category_clubs">Clubs</label>
							<input type="radio" name="category" v-model="category" value="Accessories" id="category_accessories"  :checked="category == 'Accessories'"><label for="category_accessories">Accessories</label>
							<input type="radio" name="category" v-model="category" value="Apparel" id="category_apparel" :checked="category == 'Apparel'"><label for="category_apparel">Apparel</label>
							<input type="radio" name="category" v-model="category" value="Footwear/Tennis" id="category_footwear_tennis" :checked="category == 'Footwear/Tennis'"><label for="category_footwear_tennis">Footwear/Tennis</label>
							<input type="radio" name="category" v-model="category" value="Visual/Signage" id="category_visual_signage" :checked="category == 'Visual/Signage'"><label for="category_visual_signage">Visual/Signage</label>
						</p>

						<button type="submit" class="alert button" :disabled="action_item == '' || loading == true">Add Action Item</button>
					</fieldset>
				</form>

				<!-- Clubs -->
    			<fieldset class="fieldset" id="list" v-if="clubs_items.length > 0">
					<legend>Clubs Items</legend>
					<ul>
						<li v-for="single in clubs_items">
							<store-visit-action-items-single :item="single"></store-visit-action-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Accessories -->
				<fieldset class="fieldset" id="list" v-if="accessories_items.length > 0">
					<legend>Accessories Items</legend>
					<ul>
						<li v-for="single in accessories_items">
							<store-visit-action-items-single :item="single"></store-visit-action-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Apparel -->
				<fieldset class="fieldset" id="list" v-if="apparel_items.length > 0">
					<legend>Apparel Items</legend>
					<ul>
						<li v-for="single in apparel_items">
							<store-visit-action-items-single :item="single"></store-visit-action-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Footwear/Tennis -->
				<fieldset class="fieldset" id="list" v-if="footwear_tennis_items.length > 0">
					<legend>Footwear/Tennis Items</legend>
					<ul>
						<li v-for="single in footwear_tennis_items">
							<store-visit-action-items-single :item="single"></store-visit-action-items-single>
						</li>
					</ul>
				</fieldset>

				<!-- Visual/Signage -->
				<fieldset class="fieldset" id="list" v-if="visual_signage_items.length > 0">
					<legend>Visual/Signage Items</legend>
					<ul>
						<li v-for="single in visual_signage_items">
							<store-visit-action-items-single :item="single"></store-visit-action-items-single>
						</li>
					</ul>
				</fieldset>

				<div class="send-button-container">
					<button class="button" type="button" @click="send" :disabled="items.length <= 0 || loading">Send Action Items</button>
				</div>
	        </div>
	      </div>  
	    </div>
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	import Airtable from 'airtable'
	import StoreVisitActionActionItemsSingle from './StoreVisitActionActionItemsSingle'
	import {WS_URL} from '../../config'

	export default{
		name: 'store-visit-action-action-items',
		components:{
			'store-visit-action-items-single': StoreVisitActionActionItemsSingle
		},
		data: function(){
			return {
				app_id: 'appHvlnfstue6skcB',
				app_key: 'keyIyJzQotiRwef4b',
				category: 'Clubs',
      			action_item: '',
      			items: [],      
      			loading: false			
			}
		},
		props: [
			'store'
		],
		computed: {
			clubs_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Clubs'
					})
				}else{
					return []
				}
			},
			accessories_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Accessories'
					})
				}else{
					return []
				}
			},
			apparel_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Apparel'
					})
				}else{
					return []
				}
			},
			footwear_tennis_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Footwear/Tennis'
					})
				}else{
					return []
				}
			},
			visual_signage_items: function(){
				if(this.items){
					return this.items.filter(function(item){
						return item['fields']['Category'] != undefined && item['fields']['Category'][0] == 'Visual/Signage'
					})
				}else{
					return []
				}
			},
		},
		mounted: function(){
			Bus.$on('storeVisitActionActionItemsUpdateData',this.getData);
		},
		created: function(){
			this.getData();
		},
		methods: {
			close: function(){
				Bus.$emit('storeVisitActionActionItemsClose');
			},
			create: function(){
				var self = this;
				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				this.loading = true;
				base('Action Items').create({
					"Item": self.action_item,
					"Category": [self.category],
			        "Store Visit Action": [self.store.id]
			    }, function(err, record) {
			        if (err) { 
			        	console.log(err); 
			        	alert("Unable to cretae new action item")
			        	self.loading = false;
			        	return;
		        	}
					self.action_item = "";
					self.category = "Clubs"
		        	self.loading = false;
		        	self.getData();
			    });
			},
			getData: function(){
				var self = this;
				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				var args = {
					view: 'Grid view',
					filterByFormula: '{Store Visit Action ID}="'+this.store.id+'"',
				};
				
				self.items = [];
				base('Action Items').select(args).eachPage(function page(records, fetchNextPage) {
					self.items = records;
					fetchNextPage();
				});
			},

			send: function(){
				var self = this;
				if(this.items == ""){
					alert("Please enter some items to send.")
					return false;
				}				

				if(confirm("Are you sure to send action items?") == false){
					return false;
				}
				
				self.loading = true;

				var itemsToSend = {
					'clubs_items': this.clubs_items,
					'accessories_items': this.accessories_items,
					'apparel_items': this.apparel_items,
					'footwear_tennis_items': this.footwear_tennis_items,
					'visual_signage_items': this.visual_signage_items
				};

				var sendItem = new Promise(function(resolve,reject){
					const data = {
						'items': itemsToSend,
						'date' : self.store.fields['Date of Visit'],
						'field_support_associate' : self.store.fields['Field Support Associate'],
						'store_number': self.store.fields['Store Number']
					};

					axios.post(WS_URL+'/store-visit-action/send-action-items',data).then(function(response){
						self.loading = false
						alert("Items mail sent.")
					}).catch(function(){						
						self.loading = false
						alert("Unable to send mail.")
					});
				});
			}
		}
	}
</script>
<style type="text/css" lang="scss" scoped="">  
.store-visit-action-action-items{
  z-index: 9999;
}
  #action-items-modal{
    background: #E5E5E5;
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 9999;
    padding-top: 65px;
    overflow-y: scroll;

    fieldset{
      margin-bottom: 50px;
      
      legend{
        background: transparent;
        font-size: 1.4rem;
        text-transform: uppercase;
        font-weight: bold;
        color: #1B376B;
      }

      	ul{
	    	padding: 0;
	    	margin: 0;
	    	list-style: none;

	    	li{
				display: block;
				transition: all .4s cubic-bezier(.25,.8,.25,1);
				transition-property: box-shadow;
				background-color: #fff;
				width: 100%;
				margin: 0 4px 16px;
				vertical-align: top;
				overflow: auto;
				position: relative;
				z-index: 1;
				box-shadow: 0 1px 5px rgba(0,0,0,.2), 0 2px 2px rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.12);				
				position: relative;				

				&:hover{
				    z-index: 2;
    				box-shadow: 0 5px 5px -3px rgba(0,0,0,.2), 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12);
				}
				
	    	}
	    }
    }

    .btn-close{
      position: absolute;
      padding: 25px;
      left:0;
      top: 0;
      font-size:32px;
      cursor: pointer;
    }

    .send-button-container{
    	text-align: center;
    }


  }
</style>


