<template>
	<div class="store-visit-action-items-single">
		<div class="item-content" :class="{'edit': is_edit}">
			<textarea v-if="is_edit" v-model="content_update" ref="content" :disabled="loading"></textarea>
			<span v-else>{{ content }}</span>
		</div>
		<div class="button-container" v-if="is_edit == false">			
			<button class="button" @click="edit">Edit</button> 
			<button class="alert button" @click="remove()" type="button">Delete</button>
		</div>
		<div class="button-container" v-else>
			<button class="button" @click="update" :disabled="loading">Update</button> 
			<button class="alert button" :disabled="loading" @click="cancel" type="button">Cancel</button>
		</div>
	</div>
</template>
<style type="text/css" lang="scss" scoped>
	.store-visit-action-items-single{
		.item-content{
			min-height: 100px;
			border-bottom: 1px solid rgb(206, 206, 206);
			padding: 10px;		
			flex: 1 1 auto;
			display: flex;
			flex-direction: column;

			&.edit{
				padding: 0;
			}				

			textarea{
				display: flex;
				flex: 1;
				margin: 0;
			}
		}

		.button-container{
			bottom: 0;
			width: 100%;
			text-align: right;
			padding: 10px;

			button{
				margin-bottom: 0;
			}
		}
	}
</style>
<script type="text/javascript">
	import Airtable from 'airtable'

	export default{
		props: ['item'],
		data: function(){
			return {
				is_edit: false,
				content: '',
				content_update: '',
				loading: false,
				app_id: 'appgdTM8dhKp7xW2c',
      			app_key: 'keyIyJzQotiRwef4b',
			}
		},
		mounted: function(){
			this.content = this.item.get('Item');
		},
		watch: {
			item: function(old){
				this.content = old.get('Item')
			}
		},
		methods:{
			edit: function(){
				var self = this;
				this.content_update = this.content;
				this.is_edit = true;
				setTimeout(function(){
					self.$refs.content.select();
				},200)
				
			},			
			remove: function(){
				var self = this;
				if(confirm("Are you sure to delete?") == true){
					this.item.destroy(function(err) {
				        if (err) {
				            console.log('Error destroying ', recordId, err);
				            alert("Unable to delete")
				        } else {
				        	Bus.$emit('storeVisitActionServiceActionItemsUpdateData');
				        }
				    });
				}
			},
			update: function(){
				var self = this;
				if(this.content_update == ""){
					alert("Content can not be empty.")
					return false;
				}				

				Airtable.configure({ apiKey: this.app_key });
				const base = Airtable.base(this.app_id);
				self.loading = true;

				var updateItem = new Promise(function(resolve,reject){
					var data = {
						'Item': self.content_update
					};
					base("Action Items").update(self.item.id,data,function(err,record){
						if (err) { 
							self.loading = false;
							reject(err);
						}					
						self.content = self.content_update	
						self.loading = false;
						self.is_edit = false;						
						resolve(record)
					});	
				});
			},
			cancel: function(){
				this.is_edit = false
				this.content_update = this.content
			}
		}
	}
</script>


